

















































import Vue from 'vue'
import { createMenu, getMenuInfo } from '@/services/menu'

// interface Menu {
//   id: number
//   name: string
// }
interface MemuData {
  parentMenuList: []
}

export default Vue.extend({
  data() {
    return {
      form: {
        parentId: -1,
        name: '',
        href: '',
        icon: '',
        orderNum: 0,
        description: '',
        shown: true
      },
      parentMenuList: []
    }
  },
  created() {
    this.loadMenuInfo()
  },
  methods: {
    async loadMenuInfo() {
      const { data } = await getMenuInfo<MemuData>()
      if (data.code === '000000') {
        this.parentMenuList = data.data.parentMenuList
      }
      console.log(data)
    },
    handleChange() {
      console.log(1)
    },
    async onSubmit() {
      const { data } = await createMenu({
        ...this.form
      })
      if (data.code === '000000') {
        this.$message.success('提交成功')
        this.$router.back()
      }
    }
  }
})
